import React from 'react'
import { useState } from 'react';
import { Stepper, Button, Group, Container, createStyles, Grid, Image, Text } from '@mantine/core';
import Capture1 from "../../assets/Captures/Inherit.png";
import Capture2 from "../../assets/Captures/Register.png";
import Capture3 from "../../assets/Captures/Monitor.png";

const useStyles = createStyles((theme) => ({
        wrapper: {
            padding: `${theme.spacing.xl * 2}px ${theme.spacing.xl * 2}px`,
            backgroundColor: theme.colors.blue[2]  
        },
        label: {
            color:  theme.colors.blue[7],
            fontWeight: 900
        },
        description: {
            color:"#FFFFFF"
        },
        heading: {
            padding: `${theme.spacing.md}px ${theme.spacing.md}px`,
            fontFamily: `Greycliff CF, ${theme.fontFamily}`,
            fontSize: 36,
            fontWeight: 900,
            lineHeight: 1.1,
            marginBottom: theme.spacing.md,
            color: theme.colorScheme === 'dark' ? theme.white : theme.colors.blue[7],
          },
    }));

const copyData  = [
    {
        image: Capture1,
        label: "Inherit from Client Base",
        description:
        "Inherit from the khronus client base and deploy your contract",
        content:"Inherit the Khronus Client base. You can inherit the Khronus client base directly from NPM style imports if you are using Remix or Truffle, or you can import to your project from Khronus Github if you are using other frameworks such as Brownie."
    },
    {
        image: Capture2,
        label: "Register Your Contract",
        description:
        "Register and fund your client contract in the Dapp",
        content:"After the deployment of your Client Contract, you can register it easily in the Dapp for it to start serving and receiving Khronus alerts. You need to fund the contract with the native token of its network (through the Dapp only). Mumbai Testnet is the only available network during the Public Alpha."
    },
    {
        image: Capture3,
        label: "Monitor",
        description:
        "Monitor your Alerts and their Status in the Dapp",
        content:"After the contract is registered it can receive alerts. You can monitor your registered contracts and their alerts through the Dapp."
    },
  ];

const HowItWorks = () => {
    const [active, setActive] = useState(0);
    const nextStep = () => setActive((current) => (current < 3 ? current + 1 : current));
    const prevStep = () => setActive((current) => (current > 0 ? current - 1 : current));  
    const { classes } = useStyles();
    const labels = copyData.map((item) =>  <Text className={classes.label}> {item.label}</Text>);
    const decriptions = copyData.map((item) =>  <Text className={classes.description}> {item.description}</Text>);
    return (
        <div className={classes.wrapper}>
        <Container>
            <Text className={classes.heading}>How it Works</Text>
            <Stepper color="blue" active={active} onStepClick={setActive} breakpoint="sm">
                <Stepper.Step label={labels[0]} description={decriptions[0]} allowStepSelect={active > 0}>
                    <Grid>
                        <Grid.Col span={6}><Image radius="md"src={Capture1}></Image></Grid.Col>
                        <Grid.Col span={6}><Text className={classes.description}>{copyData[0].content}</Text></Grid.Col>
                    </Grid>
                </Stepper.Step>
                <Stepper.Step label={labels[1]} description={decriptions[1]} allowStepSelect={active > 0}>
                    <Grid>
                        <Grid.Col span={6}><Image radius="md"src={Capture2}></Image></Grid.Col>
                        <Grid.Col span={6}><Text className={classes.description}>{copyData[1].content}</Text></Grid.Col>
                    </Grid>
                </Stepper.Step>
                <Stepper.Step label={labels[2]} description={decriptions[2]} allowStepSelect={active > 0}>
                    <Grid>
                        <Grid.Col span={6}><Image radius="md"src={Capture3}></Image></Grid.Col>
                        <Grid.Col span={6}><Text className={classes.description}>{copyData[2].content}</Text></Grid.Col>
                    </Grid>
                </Stepper.Step>
                <Stepper.Completed>
                    <Text className={classes.description}>All set, let us know what you think</Text>
                </Stepper.Completed>
            </Stepper>
            <Group position="center" mt="xl">
                <Button variant="default" onClick={prevStep}>Back</Button>
                <Button onClick={nextStep}>Next step</Button>
            </Group>
        </Container>  
        </div>
    )
}

export default HowItWorks