import React from 'react'
import { createStyles, Text, SimpleGrid, Container, Image } from '@mantine/core';
import { Truck, Certificate, Coin } from 'tabler-icons-react';
import MoralisLogo from "../../assets/images/Moralis-Logo-LightBG-small.png" 
import PolygonLogo from "../../assets/images/Primary Logo.png" 
//import AppWorksLogo from "../../assets/images/appworks.svg"
import AppWorksLogo from "../../assets/images/appworks01.png"

const useStyles = createStyles((theme) => ({
  wrapper: {
    padding: `${theme.spacing.xl * 2}px ${theme.spacing.xl}px`,    
    backgroundColor: theme.colors.gray[0]
  },

  feature: {
    position: 'relative',
    paddingTop: theme.spacing.xl,
    paddingLeft: theme.spacing.xl,
  },

  content: {
    position: 'relative',
    zIndex: 2,
  },

  icon: {
    color: theme.colors[theme.primaryColor][theme.colorScheme === 'dark' ? 4 : 6],
  },

  title: {
    color: theme.colorScheme === 'dark' ? theme.white : theme.black,
  },
}));

interface FeatureProps extends React.ComponentPropsWithoutRef<'div'> {
  image: string;
  title: string;
  description: string;
}

function Feature({ image, title, description, className, ...others }: FeatureProps) {
  const { classes } = useStyles();

  return (
    <div >
      <Text weight={700} size="lg" mb="xs" mt={5} className={classes.title}>
        {title}
      </Text>
      <Image width="30" height="30" src={image}></Image>
      <Text color="dimmed" size="sm">
        {description}
      </Text>
    </div>
  );
}

const mockdata = [
  {
    image: PolygonLogo,
    title: 'With the Support of:',
    description:
      'Khronus Public Alpha launch in Mumbai Tesnet was sponsored by Polygon Foundation.',
  },
  {
    image: AppWorksLogo,
    title: 'Part of:',
    description:
      "The Khronus Project is a proud member of the AppWorks community. Te biggest Start-Up Community in South East Asia",
  },
  {
    image: MoralisLogo,
    title: 'Built with:',
    description:
      'Khronus Public Alpha front-end is built with Moralis.',
  },
];

const Logos = () => {
    const items = mockdata.map((item) => <Feature {...item} key={item.title} />);
    const {classes} = useStyles();
    return (
      <div className={classes.wrapper}>
        <Container>
          <SimpleGrid cols={3} breakpoints={[{ maxWidth: 'sm', cols: 1 }]} spacing={50}>
            {items}
          </SimpleGrid>
        </Container>
      </div>
    );
}

export default Logos